<template>
  <div class="categories-section" :class="putClass">
    <a
      class="categories-item"
      v-for="(value, name, index) in categories"
      :key="index"
      :style="{
        'background-image': `url(./categories/${value.photo})`,
      }"
      :href="`${value.href}`"
    >
      <div class="delivery_name">{{ value.name }}</div>
    </a>
  </div>
</template>
<script>
export default {
  name: "MenuDelivery",
  created() {
    setTimeout(() => {
      this.isActive = true;
    }, 10);
  },
  props: {
    categories: {
      type: Object,
      default: () => ({}),
    },
  },
  data: () => ({
    isActive: false,
  }),
  computed: {
    putClass() {
      return this.isActive ? "isActive" : "not-active";
    },
  },
};
</script>

